.pagination {
    display: flex;
    justify-content: center;
    gap: 14px;
}

.mid-btns {
    display: flex;
    gap: 8px;
}
.left-btns {
    display: flex;
    gap: 4px;
}
.right-btns {
    display: flex;
    gap: 4px;
}
.last-option-page {
    display: none;
}

button.current-page {
    border: 0px;
    background-color: var(--lt-green);
}
.pagination-btn {
    font-weight: 600;
    height: 40px;
    width: 40px;
    border: 1px solid rgba(5, 5, 5, 0.3);
    border-radius: 50%;
}

.side-btn:disabled {
    pointer-events: none;
    background-color: var(--main-cl);
}

@media screen and (min-width: 768px) {
    .pagination {
        gap: 24px;
    }
    .pagination-btn {
        height: 44px;
        width: 44px;
    }
    .last-option-page {
        display: block;
    }
    .right-btns {
        gap: 8px;
    }
    .mid-btns {
        gap: 10px;
    }
    .left-btns {
        gap: 8px;
    }
}