.modal_reciepe{
outline: 1px solid tomato;

min-width: 320px;
    min-height: 586px;
    position: absolute;
    top: 50%; 
    left: 50%;
    transform: translate(-50%, -50%);

border-radius: 15px;
background: #FFF;
padding-top: 40px;
padding-left: 20px;
padding-bottom: 40px;
padding-right: 20px;
}
@media screen and (min-width: 768px){
.modal_reciepe{
width: 685px;
    height: auto;
    padding-top: 32px;
        padding-left: 32px;
        padding-bottom: 32px;
        padding-right: 32px;
}
 .main-info{
    display: flex;
    margin-bottom: 24px;
 }
}


.modal__close{
    width: 20px;
        height: 20px;
        position: absolute;
        top: 20px;
        right: 20px;
        background-color: inherit;
        border: none;
        cursor: pointer;
        padding: 0;
        stroke: #050505;
}
.modal_x{
   stroke: #050505;
}



/* video */
.media{
    width: 295px;
    height: 295px;
margin-bottom: 20px;
    border-radius: 8px;
    }
@media screen and (min-width: 768px){
.media{
    margin-right: 32px;
    width: 332px;
        height: 300px;
        margin-bottom: 0;
}
}
.video{
width: 295px;
    height: 295px;
    flex-shrink: 0;
    border-radius: 8px;
}
@media screen and (min-width: 768px){
.video{
    width: 332px;
        height: 300px;
}
}

/* name */
.name-reciepe{
    padding-bottom: 14px;
    text-transform: uppercase;
    color: var (--main-cl);
    font-weight: 600;
    font-size: 18px;
}
@media screen and (min-width: 768px){
.name-reciepe{
padding-bottom: 16px;
}
}

/* rating ans time */
.rating-time{
    display: flex;
    gap: 16px;
    padding-bottom: 33px;
    color: rgba(5, 5, 5, 0.50);
align-items: center;
       font-weight: 500;
}

.rating{
    display: flex;
    gap: 1px;
    align-items: center;
}

.icon-star {
    fill: #0505051A;
}


/* ingredients */
.ingredients{
padding-bottom: 40px;

}
 @media screen and (min-width: 768px){
.ingredients{
overflow-x: hidden;
overflow-y: scroll;
    height: 204px;
    padding-right: 0px; 
   
 }
}  
.ingredient{
    border-bottom:1px solid #0505051A;
    margin-bottom: 14px;
    padding-right: 20px;
}

.ingredient-name{
    color: #050505;
        font-size: 14px;
       line-height: 1.28;
}

.ingredient-measure{
    color: rgba(5, 5, 5, 0.50);
        font-size: 14px;
      
        line-height: 1.28;
}

.ingredient-list{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-bottom: #050505;
    padding-bottom: 6px;
    }

    

/* tags */
.tags{
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    padding-bottom: 24px;
    }

@media screen and (min-width: 768px){
.tags{
padding-bottom: 32px;
}
}
.tags-name{
    border-radius: 35px;
        border: 1px solid rgba(5, 5, 5, 0.20);
        padding: 8px 16px;
            justify-content: center;
            align-items: center;
    color: #050505;
        font-size: 12px;
 
        font-weight: 500;
       
}



/* instruction */

.instructions{
    width: 295px;

    color: rgba(5, 5, 5, 0.80);
    font-weight: 300;
        font-size: 14px;
        margin-bottom: 40px;
}
@media screen and (min-width: 768px){
.instructions{
width: 621px;
margin-bottom: 32px;
}
}

/* buttons */
.modal-btn{
    display: flex;
flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 0px;
    margin: 0px;
    list-style: none;
}
@media screen and (min-width: 768px){
.modal-btn{
    justify-content: flex-start;
}
}
.btn-favorite{
display: block;
padding: 12px 24px;
border: none;
background-color: #9BB537;
border-radius: 15px;
color:var(--back-cl);
font-weight: 500;
font-size: 14px;
cursor: pointer;
}

.btn-favorite:hover{
    color: #050505;
}

.rating-btn{
    
display: block;
    padding: 12px 24px;
border: 1px solid var(--lt-green);
    background-color: transparent;
    border-radius: 15px;
    color: var(--main-cl);
    font-weight: 500;
    font-size: 14px;
    cursor: pointer;
    margin: 0;
}

